
.fade-enter{
    opacity: 0
}
.fade-enter-active{
    transition: opacity 1s
}
.fade-enter-to{
    opacity: 1
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.home{
    overflow: hidden;
    height: 50px;
    line-height: 50px;
    text-align: center;
    background-color: #006666;
    list-style: none;
}
.home li{
cursor: pointer;
    float: left;
    width: 200px;;
    color: #fff;
    font-size: 35px;
    border-right: 1px solid black;
}



